import './like.css'
import React, {useState} from "react";
import {settings} from "../company/companyPage";

export const Like = () => {

    const [shake, setShake] = useState(false);

    const animate = (set: any) => {
        set(true);
        setTimeout(() => set(false), 1000);
    }

    return <div className="like-section">
        <div className="body-container">
            <div className="section-container">
                <div className="heading-container">
                    <div className="w-layout-grid grid-container">
                        <div id="w-node-f19d6cfa-bcbe-7f85-c51d-55e5dca60092-dca6008d" className="div-block-96">
                            <div {...settings}  className="subtitle-bord">
                                <h4 {...settings} className="heading-2---temy">LEARN MORE<br/>ACT NOW</h4></div>
                        </div>
                        <div {...settings} id="w-node-f19d6cfa-bcbe-7f85-c51d-55e5dca60095-dca6008d" className="div-block-95"><h1
                             className="main-heading-1">
                            Do you have any questions?<br/>
                        </h1>
                            <div {...settings}  className="div-block-25 lite-header">
                                <p className="grad-text">Let’s talk!</p></div>
                            <div {...settings}>
                            <a  onClick={() => animate(setShake)}  href="/new/contact"
                               className={[shake && 'shake' ,"main-button w-button", 'contrast'].join(" ")}>
                                Get in Touch
                            </a>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
}