import React, {useEffect, useState} from 'react'
// import s from './header.module.css'
// @ts-ignore
import $ from 'jquery';
import './header.css'
import cn from "classnames";
import {NavLink} from 'react-router-dom'
import {FontAwesomeIcon} from '@fortawesome/react-fontawesome'
import {faBars} from '@fortawesome/free-solid-svg-icons'

export const Animate = (event: any) =>  {
    event.target.style = ' ';
    void event.target.offsetWidth;
    event.target.style.animation = 'shake .8s';
}



export const Header = ({page}:{page:string}) => {

    const [active, setActive] = useState(false)
    const [width, setWidth] = useState<any>($(window).width())

    const resize = () => {
        setWidth($(window).width())
    }


    useEffect(() => window.addEventListener('resize', resize), [])

    const handleEvent = (e:any) => e.target.classList.remove('current');

    const Nav = () => {
        return <React.Fragment>
            <a onMouseEnter={handleEvent} onClick={Animate}
               className={['nav-company w-nav-link', page === 'company' && 'w--current'].join(" ")} href="/new/">Company</a>
            <a onMouseEnter={handleEvent}  onClick={Animate}
               className={[ 'nav-works w-nav-link', page === 'works' && 'w--current'].join(" ")} href="/new/">Work</a>
            <a onMouseEnter={handleEvent}  onClick={Animate}
               className={['nav-careers w-nav-link', page === 'careers' && 'w--current'].join(" ")} href="/new/">Careers</a>
            <a onMouseEnter={handleEvent} onClick={Animate}
               className={[ 'nav-works contact-button w-nav-link', page === 'contact' && 'w--current'].join(" ")}
               href="/new/contact">Contact us</a>
        </React.Fragment>
    }

    return <div className={'navbar_x w-nav'}>
        <div className={cn({['active']: active}, 'container_x w-container')}>
            <a href="/" className="brand w-nav-brand w--current"><img alt={"temy-logo-simple"} className={'logo'}
                                                src={"https://assets.website-files.com/5d2593d4e31f05ed4edd50f5/5dbc29afc0a72841e8d7e045_temy-logo-simple.svg"}/></a>
            {width > 991 && (<nav className={cn({['active']: active}, 'nav-menu w-nav-menu',)}>
                <Nav/>
            </nav>)}
            <div onClick={() => setActive(!active)} className={cn({['active']: active}, 'menu-button w-nav-button')}><FontAwesomeIcon
                icon={faBars}/></div>
            <div className={'clearfix'}/>
        </div>
        {width < 991 && (<nav className={cn({['active']: active}, 'nav-menu w-nav-menu collapsed')}>
            <Nav/>
        </nav>)}
    </div>
}